#app-container {
	background-position: center center;
	background-size: 1457px 964px;
	background-repeat: repeat;
	background-color: #d0d2d3;
}

#rotate-bg {
	position: absolute;
	height: 500px;
	width: 100%;
}
#titles-container {
	display: block;
	margin: 0 30px;
	padding: 30px;
}

#titles-container h1 {
	font-family: "HelveticaNeueLTStd";
	font-size: 40px;
	text-align: center;
	font-weight: 500;
	color: #53555e;
}

#titles-container p.lead {
	font-family: "HelveticaNeueLTStd";
	font-size: 20px;
	text-align: center;
	margin: 5px 0px;
	color: #53555e;
}
@media (max-width: 1000px) {
	#titles-container h1 {
		font-size: 32px;
	}
	#titles-container p.lead {
		font-size: 22px;
	}
}



