g#wheel-container {
}

g#wheel-container text {
	pointer-events: none;
	font-family: "HelveticaNeueLTStd";
	font-weight: 700;
	fill: #53555E
}

g#wheel-container path.arc-body {
	fill: #fff;
	cursor: pointer;
	stroke: #fff;
}

g#wheel-container path.arc-body.selected-body-goal {
	stroke: #555;
	stroke-width: 1px;
}


g#wheel-container path.arc-body-disabled {
	cursor: not-allowed;
}

g#wheel-container text.arc-text-disabled {
	fill: #cacaca;
}

g#wheel-container text.arc-text {
	fill: #fff;
}

g#wheel-container path.arc-triangle-goal {
	fill: #fff;
}

g#wheel-container path.arc-triangle-goal.selected-triangle-goal {
	fill: #555;
}



