#loading {
  background-position: center center;
  background-size: 1457px 964px;
  background-repeat: repeat;
  background-color: #d0d2d3;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-family: arial;
}
#loading svg {
  display: block;
  width: 100px;
  height: 100px;
}
#loading.hide {
  display: none;
}
#loading.show {
  display: -ms-flexbox;
  display: flex;
}



