g#tooltips-container {
	font-family: "HelveticaNeueLTStd";
}

g#tooltips-container text {
	pointer-events: none;
}
g#tooltips-container text {
	fill: #53555e;
	font-size: 20px;
	font-weight: 500;
}

g#tooltips-container image {
	pointer-events: none;
}

g#tooltips-container path {
	fill: none;
	stroke: #999999;
}

g#tooltips-container circle {
	fill: #999999;
}

g#tooltips-container #tooltips-extras {
	opacity: 0;
}



