div#suggestions-container {
	position: absolute;
	top: 40%;
	opacity: 0;
	-webkit-transition: opacity 0.2s linear;
	-o-transition: opacity 0.2s linear;
	transition: opacity 0.2s linear;
	z-index: 1;
}

div#suggestions-container #text-container {
	font-family: "HelveticaNeueLTStd";
	color: #53555e;
	background: #ccc;
	padding: 20px;
	border-radius: 15px;
	-webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.3);
	        box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.3);
	margin: 0 20px;
	max-width: 200px;
	text-align: center;
	position: relative;
}

div#suggestions-container.mobile {
	position: static;
	width: 100%;
	display: block;
	opacity: 1 !important;
	min-height: 200px;
}

div#suggestions-container.mobile #text-container {
	margin: 0px auto 15px auto;
	font-size: 22px;
	-webkit-box-shadow: none;
	        box-shadow: none;
	max-width: 90%;
	background: none;
	padding: 0px;
}

div#suggestions-container.loaded {
	opacity: 1;
}



