g#details-circle-container {
	font-family: arial;
}

g#details-circle-container circle.details {
	fill: #fff;
	stroke: #fff;
}
g#details-circle-container circle.details.low-circle-details {
	stroke: #555;
	stroke-width: 1px;
}

g#details-circle-container #back-button {
	cursor: pointer;
}

g#details-circle-container .topic-title {
	font-size: 25px;
	font-family: "HelveticaNeueLTStd";
	font-weight: 700;
}
g#details-circle-container .topic-title {
	fill: #53555e;
}

g#details-circle-container .goal-title {
	font-family: "HelveticaNeueLTStd";
	font-size: 15px;
}
g#details-circle-container .text-rect {
	fill: steelblue;
}

g#details-circle-container .arrow-action {
	fill: transparent;
	pointer-events: bounding-box;
	cursor: pointer;
}



