div#restart-btn-container {
	font-family: "HelveticaNeueLTStd";
	cursor: pointer;
	color: #53555e;
	padding: 20px;
	margin: 0 20px;
	max-width: 200px;
	text-align: center;
	opacity: 0;
	-webkit-transition: opacity 0.2s linear;
	-o-transition: opacity 0.2s linear;
	transition: opacity 0.2s linear;
	position: relative;
	font-weight: bold;
}

div#restart-btn-container img {
	margin-right: 0.4em;
}

div#restart-btn-container.mobile {
	position: absolute;
	font-size: 18px;
	margin: 0;
	top: 0%;
}

div#restart-btn-container.loaded {
	opacity: 1;
}

div#restart-btn-container.active {
	opacity: 1;
}



